@use './styles/toast' as *;
@import 'react-toastify/dist/ReactToastify.css';

.App {
  padding: 40px;
}

.App-link {
  color: #61dafb;
}

// Override WP default styles
.wpengine-smart-search-page {
  // FIX: MUI checkbox onChange
  input[type='checkbox'] {
    height: 200%;
  }

  input:focus {
    outline: 3px solid #ebf5fa;
    box-shadow: 0 0 0 1px #2271b1;
    border-color: #2271b1 !important;
  }

  input:focus {
    outline: 3px solid #ebf5fa;
    box-shadow: 0 0 0 1px #2271b1;
    border-color: #2271b1 !important;
  }
}

.toplevel_page_wpengine-smart-search {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto,
    Oxygen-Sans, Ubuntu, Cantarell, 'Helvetica Neue', sans-serif;

  label {
    font-size: 13px;
    line-height: 22.5px;

    strong {
      font-weight: 700;
    }
  }

  #wpcontent {
    padding-left: 0;
  }

  button {
    border-radius: 6px;
  }
}
